<template>
  <div id="a">
    <div id="login_box">
      <h2>LOGIN</h2>
      <div id="input_box">
        <input type="text" v-model="username" placeholder="请输入用户名" />
      </div>
      <div class="input_box">
        <input type="password" v-model="password" placeholder="请输入密码" />
      </div>
      <button @click="handleLogin">登录</button><br />
    </div>
  </div>
</template>
<script>
import axios from "axios";

import router from "@/router";
import { Message } from "element-ui";

export default {
  name: "LoginComponent",
  data() {
    return {
      username: "admin",
      password: "123456",
    };
  },
  methods: {
    handleLogin() {
      axios
        .post("/api/login", {
          username: this.username,
          password: this.password,
        })
        .then((res) => {
          if (res.data.code == 1) {
            //异步请求后，需要修改共享数据
            //在actions可以调用mutation中定义函数
            router.push({
              path: "/main",
            });
          } else {
            Message.error(res.data.msg);
          }
        })
        .catch((res) => {
          Message.error("登录失败",res.data.msg);
        });
    },
  },
};
</script>
<style scoped>
* {
  padding: 0px;
  margin: 0px;
}
#a {
  width: 100%;
  height: 100%;
  background: url("https://photo.tuchong.com/19720960/f/905511450.jpg")
    no-repeat;
  background-size: 100% 130%;
  display: flex;
  justify-content: center;
  align-items: center;
}

#login_box {
  width: 20%;
  height: 400px;
  background-color: #00000060;
  /* margin: auto; */
  /* margin-top: 10%; */
  text-align: center;
  border-radius: 10px;
  padding: 50px 50px;
}

h2 {
  color: #ffffff90;
  margin-top: 5%;
}

#input-box {
  margin-top: 5%;
}

span {
  color: #fff;
}

input {
  border: 0;
  width: 60%;
  font-size: 15px;
  color: #fff;
  background: transparent;
  border-bottom: 2px solid #fff;
  padding: 5px 10px;
  outline: none;
  margin-top: 10px;
}

button {
  margin-top: 50px;
  width: 60%;
  height: 30px;
  border-radius: 10px;
  border: 0;
  color: #fff;
  text-align: center;
  line-height: 30px;
  font-size: 15px;
  background-image: linear-gradient(to right, #30cfd0, #330867);
}
</style>